import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import Moneyinput from "../layout/Moneyinput";
import FormControl from "@material-ui/core/FormControl";
import Expenses from "./Expenses";

import AddExpense from "../components/AddExpense";
import AddSaving from "../components/AddSaving";
import Savings from "./Savings";
import AddDebt from "./AddDebt";
import Debts from "./Debts";

class Budget extends Component {
  calc_total_expenses(expenses) {
    if (expenses.length == 0) {
      return 0;
    } else {
      return expenses.reduce((total, num) => {
        return total + parseFloat(num.amount, 10);
      }, 0);
    }
  }

  sumProperty(arr, type) {
    return arr.reduce((total, obj) => {
      if (typeof obj[type] === "string") {
        return total + parseFloat(obj[type], 10);
      }
      return total + obj[type];
    }, 0);
  }

  //   let totalAmount = ( sumProperty(array, 'amount') ).toFixed(2);

  render() {
    const {
      net_income_savings,
      monthly_user_info,
      expenses,
      savings,
      debts
    } = this.props;
    const {
      deduceted_paye,
      original_income_per_month,
      original_paye,
      retirement_savings_rate,
      rebate
    } = net_income_savings;

    const {
      monthly_salary,
      provident_fund,
      pension_fund,
      ra_fund
    } = monthly_user_info;

    const totalExpenses = this.sumProperty(expenses, "amount").toFixed(2);
    const totalSavings = this.sumProperty(savings, "amount").toFixed(2);
    const totalDebtRepayment = this.sumProperty(debts, "amount").toFixed(2);

    console.log("These are the savings so far: ", savings);

    const total_retirement_savings =
      parseFloat(provident_fund, 10) +
      parseFloat(pension_fund, 10) +
      parseFloat(ra_fund, 10);

    const real_net_income =
      original_income_per_month - total_retirement_savings;

    const budget_remaining =
      real_net_income - totalExpenses - totalSavings - totalDebtRepayment;

    const formControlStyles = {
      paddingTop: "1em"
    };

    return (
      <FormControl style={formControlStyles} fullWidth={true}>
        {/* <div className="card-header">Income and Retirement Savings</div> */}

        <Moneyinput
          label="Your available amount for expenses"
          id="real_net_income"
          name="real_net_income"
          value={budget_remaining.toFixed(2)}
          type="number"
        />

        <div>
          <div className="card-header">
            {" "}
            Expenses <span> Total R{totalExpenses}</span>{" "}
          </div>
          <div className="card-body">
            <AddExpense />
          </div>

          <div>
            <Expenses />
          </div>
        </div>

        <div>
          <div className="card-header">
            {" "}
            Savings/Investents <span> Total R{totalSavings}</span>{" "}
          </div>
          <div className="card-body">
            <AddSaving />
          </div>

          <div>
            <Savings />
          </div>
        </div>

        <div>
          <div className="card-header">
            {" "}
            Loan repayments <span> Total R{totalDebtRepayment}</span>{" "}
          </div>
          <div className="card-body">
            <AddDebt />
          </div>

          <div>
            <Debts />
          </div>
        </div>
      </FormControl>
    );
  }
}

const mapStateToProps = state => ({
  net_income_savings: state.tax.net_income_savings,
  monthly_user_info: state.tax.monthly_user_info,
  savings: state.tax.savings,
  debts: state.tax.debts,
  expenses: state.tax.expenses
});

Budget.propTypes = {
  net_income_savings: PropTypes.object.isRequired,
  monthly_user_info: PropTypes.object.isRequired
};
export default connect(
  mapStateToProps,
  null
)(Budget);
