import React, { Component } from "react";
import FormControl from "@material-ui/core/FormControl";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import { add_expense } from "../actions/taxAction";
import { connect } from "react-redux";
import Input from "@material-ui/core/Input";
class AddExpense extends Component {
  state = { description: "", amount: 0 };
  onChange = e => this.setState({ [e.target.name]: e.target.value });

  onClick = e => {
    e.preventDefault();

    const { description, amount } = this.state;
    const expense = { description: description, amount: amount };

    this.props.add_expense(expense);
    this.setState({ description: "", amount: 0 });
  };
  render() {
    const { description, amount } = this.state;
    const formControlStyles = {
      display: "flex",
      flexDirection: "row"
    };

    return (
      <div>
        <FormControl style={formControlStyles} fullWidth={true}>
          <Input
            id="description"
            name="description"
            value={description}
            placeholder="Expense Description"
            type="text"
            onChange={this.onChange}
          />
          :
          <Input
            id="amount"
            name="amount"
            value={amount}
            type="number"
            onChange={this.onChange}
          />
          <Button color="primary" onClick={this.onClick}>
            Add
          </Button>
        </FormControl>
      </div>
    );
  }
}

AddExpense.propTypes = {
  add_expense: PropTypes.func.isRequired
};

export default connect(
  null,
  { add_expense }
)(AddExpense);
