import React, { Component } from "react";
import { connect } from "react-redux";

class Debt extends Component {
  render() {
    const { debt } = this.props;
    const styles = {
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between"
    };
    return (
      <div style={styles}>
        <span>
          <label>{debt.description}</label>:<label> R{debt.amount}</label>{" "}
        </span>
      </div>
    );
  }
}

export default connect()(Debt);
